export  function getWeekDay(){
    
var nowTemp = new Date();//
 
var oneDayLong = 24*60*60*1000 ;//
 
var c_time = nowTemp.getTime() ;//
 
var c_day = nowTemp.getDay()||7;//
 
var m_time = c_time - (c_day-1)*oneDayLong;//
 
var monday = new Date(m_time);//
 
var m_year = monday.getFullYear();
 
var m_month = monday.getMonth()+1;
 
var m_date = monday.getDate();
return m_year+'-'+m_month+'-'+m_date
} 

export  function getWeek() {
    var now = new Date();
    var day = now.getDay();
    var weeks = new Array("", "", "", "", "", "", "");
    var week = weeks[day];
    return week;
 }
 
export function fromTime(val){
    if(val){
      return val.slice(0,10)
    }else{
      return val
    }
  } 

 export function getNewData(dateTemp, days){  
    var dateTemp = dateTemp.split("-"); 
    var nnDate = dateTemp[1]+'-'+dateTemp[2]+'-'+dateTemp[0]; //MM-DD-YYYY    
    var nDate = new Date(nnDate.replace(/\s+/g,""));//
    var millSeconds = Math.abs(nDate) + (days * 24 * 60 * 60 * 1000);  
    var rDate = new Date(millSeconds);
    var year = rDate.getFullYear();  
    var month = rDate.getMonth() + 1;  
    if (month < 10) month = "0" + month;  
    var date = rDate.getDate();  
    if (date < 10) date = "0" + date;  
    return (year + "-" + month + "-" + date); 
}